import React, { useEffect, useState } from 'react';

import Accordion from 'components/shared/Accordion/index';
import FormItem from 'components/shared/FormControl/FormItem';
import FormInput from 'components/shared/FormControl/FormInput';
import FormSelect from 'components/shared/FormControl/FormSelect';
import { getProvince } from 'utils/location-picker';
import { useFormContext } from 'react-hook-form';
import FormNumber from 'components/shared/FormControl/FormNumber';

const BranchInfo = ({ disabled, title, id }) => {
  const methods = useFormContext();
  const [provinceOptions, setProvinceOptions] = useState([]);

  useEffect(() => {
    const provinces = getProvince();
    setProvinceOptions(provinces);
  }, []);

  return (
    <Accordion title={title} id={id}>
      <div className='cb_row'>
        <div className='cb_col_6'>
          <FormItem label='Tn chi nhnh' isRequired disabled={disabled}>
            <FormInput
              type='text'
              field='branch_name'
              placeholder='Nhp tn chi nhnh'
              validation={{
                required: 'Tn chi nhnh l bt buc',
              }}
            />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='Khu vc' isRequired disabled={disabled}>
            <FormSelect
              allowClear
              disabled={disabled}
              field='branch_province'
              placeholder='Chn khu vc'
              list={provinceOptions}
              validation={{
                required: 'Khu vc l bt buc',
              }}
            />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='a ch chi nhnh' isRequired disabled={disabled}>
            <FormInput
              type='text'
              field='address'
              placeholder='Nhp a ch chi nhnh'
              validation={{
                required: 'a ch chi nhnh l bt buc',
              }}
            />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='S in thoi chnh' isRequired disabled={disabled}>
            <FormInput
              type='text'
              field='phone_number_main'
              placeholder='Nhp S in thoi chnh'
              validation={{
                required: 'S in thoi chnh l bt buc',
              }}
            />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='S in thoi ph' disabled={disabled}>
            <FormInput type='text' field='phone_number_sub' placeholder='Nhp S in thoi ph' />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='Mc li sut' isRequired disabled={disabled}>
            <FormNumber
              field='interest_rate'
              placeholder='Nhp mc li sut'
              addonAfter='%'
              validation={{
                min: { value: 0, message: 'Gi tr phi ln hn 0' },
                max: { value: 100, message: 'Gi tr phi nh hn 100' },
                required: 'Mc li sut l bt buc',
              }}
            />
          </FormItem>
        </div>

        <div className='cb_col_6'>
          <FormItem label='S ti khon ngn hng' isRequired disabled={disabled}>
            <FormInput
              type='text'
              field='bank_number'
              validation={{
                required: 'S ti khon ngn hng l bt buc',
              }}
              placeholder='Nhp s ti khon ngn hng'
            />
          </FormItem>
        </div>

        {/* <div className='cb_col_12'>
          <FormItem label='M t' disabled={disabled}>
            <FormTextArea field='description' rows={2} placeholder='M t' />
          </FormItem>
        </div> */}
      </div>
    </Accordion>
  );
};

export default BranchInfo;
